.icon-left{
    position: absolute;
    top:40px;
    left:40px;
}
.icon-right{
    position: absolute;
    bottom: 40px;
    right: 40px;
}

.season-display.winter i {
    color:blue
}

.season-display.summer i {
    color: orangered
}

.season-display{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.summer{
    background: rgb(255,207,127);
    background: radial-gradient(circle, rgba(255,207,127,1) 0%, rgba(255,157,157,1) 100%);
}

.winter{
    background: rgb(127,247,255);
background: radial-gradient(circle, rgba(127,247,255,1) 0%, rgba(157,177,255,1) 100%);
}

.loading{

display: flex;
justify-content: center;
align-items: center;
height: 100vh;
}
